import Request from '../../util/luch-request/request.js'
import common from '../../common/globalVariable.js'
export default{
	data(){
		return{
			sex:'',
			myAvatar:'',
			pairUserId:'',
			pairNickname:'',
			pairAvatar:'',
			msgValue:'Hi，很高兴认识你。'
		}
	},
	onLoad:function(option){
		this.pairUserId = option.pairUserId;
		this.sex = common.sex;
		this.myInfo();
		this.pairUserInfo();
		console.log('sex',sex,'myAvatar',myAvatar,'pairUserID',pairUserID,'pairNickName',pairNickName,'pairAvatar',pairAvatar)
	},
	methods:{
		//发送消息
		sendMsg(){
			var THAT = this;
			var msg = common.nim.sendText({
			    scene: 'p2p',
			    to: THAT.pairUserId,
			    text: THAT.msgValue,
			    done: (error, msg)=>{
					console.log(error);
					console.log(msg);
					console.log('发送' + msg.scene + ' ' + msg.type + '消息' + (!error?'成功':'失败') + ', id=' + msg.idClient);
					
					var ind = 0;
					var flag = false;
					common.msgs.map((cur,index)=>{
						if(cur.to==msg.from){
							flag = true;
							common.msgs[index].msgs.push(msg);
						}
						ind++;
					})
					if(flag==false){
						console.log(ind);
						common.msgs[ind]={sessionId:'p2p-'+msg.to,scene:'p2p',to:msg.to,msgs:[],timetag:(new Date()).valueOf(),time:'NaN-NaN-NaN NaN:NaN'};
						console.log(common.msgs[ind].msgs);
						common.msgs[ind].msgs.push(msg);
					}
					// THAT.msgList.push(msg);
					setTimeout(()=>{
						console.log('common.msgs',common.msgs)
						common.msgs.map((cur,index)=>{
							if(cur.to===THAT.userId){
								
								cur.msgs.push(msg);
							}
						})	
						uni.showToast({
							title:'发送成功',
							icon:'success'
						})
					},100)
					setTimeout(()=>{
						uni.navigateBack({
							delta:1
						})
					},1500)
				}
			});
		},
		//获取登陆者信息
		myInfo(){
			var THAT = this;
			const http = new Request();
			let params={
				params:{
					id:common.userId,
					cerStatus:''
				}
			}
			http.get('/user', params).then(res => {
				console.log(params);
				console.log(res);
				THAT.myAvatar = res.data.result.user.icon;
			}).catch(err => {
				console.log(err);
			})
		},
		//配对用户的信息
		pairUserInfo(){
			var THAT = this;
			const http = new Request();
			let params={
				params:{
					id:THAT.pairUserId,
					cerStatus:''
				}
			}
			http.get('/user', params).then(res => {
				console.log(params);
				console.log(res);
				THAT.pairAvatar = res.data.result.user.icon;
				THAT.pairNickname = res.data.result.user.userName;
			}).catch(err => {
				console.log(err);
			})
		},
		//关闭当前页
		closePage(){
			uni.navigateBack({
				delta:1
			})
		},
		//跳转到downloadapp页面 推广下载
		toDownloadApp(){
			uni.navigateTo({
